
#userCenter {
  color: rgba(255,255,255,.87);
}
#header {
  width: 100%;
  position: fixed;
  top: 0;
  height: 64px ;
  z-index: 20000;
}
#header  .el-dialog {
  position: fixed;
  left:50%!important;
  top:40%!important;
  transform: translate(-50%,-40%);
  -webkit-transform: translate(-50%,-40%);
  width:392px;
  box-shadow: 0 4px 12px 0 rgba(0,0,0,0.20);
  border-radius: 4px;
  margin-top: 0!important;
}
#header  .el-dialog .el-button {
  padding: 8px 20px;
}
.header {
  width: 100%;
  height: 64px;
  position: fixed;
  z-index: 2001;
  background: rgba(0,0,0,1);
  color: #fff;
  box-sizing: border-box;
  transition: all 1s ease-in-out;
  padding-left: 150px;
  padding-right: 150px;
}
.header ul {
  list-style: none;
  overflow: hidden;
  float: left;
  padding: 22px 0;
  line-height: 22px;
}
.header .head_l {
  height: 100%;
  line-height: 64px;
  padding:0;
  width: calc(100% - 258px);
}
.header ul li {
  float: left;
  font-size: 14px;
  padding-left: 32px;
  padding-right: 24px;
  cursor: pointer;
  font-weight: 400;
  transition: all 0.3s;
  color: rgba(255,255,255,0.54);
}
.header ul li a {
  color: rgba(255,255,255,0.54);
}
.header ul li a:hover{
  color: rgba(255,255,255,0.87);
  font-weight: 500;
}
.header .logo {
  height: 100%;
  margin-right: 32px;
  padding: 0;
}
.logo img {
  position: relative;
  top: 17px;
}
.header .head_r {
  float: right;
}
.header .head_r li:nth-child(1) {
  padding-right: 32px;
}
.header .head_r li:nth-child(2){
  position: relative;
  padding-left: 0px;
  padding-right: 0px;
}
.header .head_r li:nth-child(3){
  position: relative;
  /*padding-left: 32px;*/
}
.header .head_r li:nth-child(2)::after{
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 8px;
  background-color: rgba(255,255,255,.15);
}
.trink-icon {
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid rgba(255,255,255,.54);
  margin-left: 8px;
  transition: all 0.2s linear;
}
.product-over-downContainer {
  transition:height 0.5s ease-in-out;
  height: 0px;
  background: rgba(24,24,24,1);
  position: absolute;
  top: 0;
  left: 0%;
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  z-index: -1;
}
.product-content {
  position: absolute;
  left: 47%;
  height: 100%;
}
.product-img-list {
  position: relative;
}
.product-img-list a{
  text-decoration: none;
  color: rgba(255,255,255,.54);
  font-size: 14px;
  position: absolute;
  top: 90px;
  line-height: 24px;
  transition:all 0.5s ease-in-out;
}
.boxLeftMouse80 {
  left:80px;
}
.boxLeftMouse0 {
  left:0px;
}
.product-img-list a:hover {
  color: rgba(255,255,255,.54) !important;
}
.product-img {
  width: 72px;
}
.product-img img {
  width: 100%;
}
.height0 {
  height: 0px;
}
.height206 {
  height: 206px;
}
.trinkIconRotate180 {
  border-top-color: rgba(255,255,255,.87);
  transform: rotate(180deg);
}
.trinkIconRotate0 {
  border-top-color: rgba(255,255,255,.54);
  transform: rotate(0deg);
}
.proColorMouse87 {
  color:rgba(255,255,255,.87)
}
.proColorMouse54 {
  color:rgba(255,255,255,.54)
}
.headerGreyMouse {
  background-color: rgba(24,24,24,1);
}
.header000Mouse {
  background-color: #000;
}
@media screen and (max-width: 768px){
.web-head-container {
    display: none!important;
}
.m-header-container {
    display: block!important;
}
.m-header-right-btn {
    text-align: right;
}
.m-header-right-btn img {
    width: 3rem;
}
.m-header-container .header {
    width: 100%!important;
    padding: 0 10px;
    z-index: 999999;
}
.header_select_list {
    padding-top: 64px;
    position: fixed;
    width: 100%;
    z-index: 99999;
    background: #1a1a1a!important;
}
.header_select_list>li {
    width: 100%;
    padding:15px;
    text-align: left!important;
}
.header_select_list>li a {
    width: 100%;
    display: block;
    color: rgba(255,255,255,0.54);
}
.header_select_list>li:hover {
    /*background: rgba(255,255,255,.87);*/
}
.product-img {
    max-width: 36px!important;
}
.can-main-container {
    width: 100%!important;
}
.font-size48 {
    font-size: 24px!important;
}
.font-size18 {
    font-size: 16px!important;
}
.index-can-content {
    width: 100% !important;
    padding:0 15px;
    text-indent: 2em;
}
.swiper_banner_one {
    width: 100%!important;
}
.qrcord_downIcon img {
    margin-top: -1px;
}
.aboutUs-btn {
    width: 90%!important;
}
.header ul li {
    padding-left: 0;
}
}




